* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
#root {
  background: url("./images/unnamed.png");
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.__main {
  max-width: 90%;
  margin: auto;
  background-color: gray;
  min-height: 100%;
  width: 100%;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  padding-left: 0;
}
.btn {
  background-color: white;
  border: none;
  outline: none;
  cursor: pointer;
  width: 230px;
  height: 47px;
  line-height: 47px;
  border-radius: 5px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.02);
  display: flex;
  padding: 0;
  transition: all 0.3s ease-in-out;
}
.btn:hover {
  background-color: gray;
  color: white;
  transform: scale(1.02);
}
.btn i {
  flex: 0.2;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 25px;
  border-right: 1px solid #ebe7fb;
}
.btn span {
  flex: 0.8;
  display: flex;
  justify-content: center;
  align-items: center;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #6c84ff;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #3b5bfe;
}
